import * as loginServ from "../services/loginService";

export default {
  namespaced: true,
  state: {
    data: null, // 用户的信息
    isLoading: false, // 是否登录
  },
  mutations: {
    setData(state, payload) {
      state.data = payload;
    },
    setIsLoading(state, payload) {
      state.isLoading = payload;
    },
  },
  actions: {
    // 登录
    async login({ commit }, payload) {
      commit("setIsLoading", true);
      const resp = await loginServ.login(payload.loginId, payload.loginPwd);
      commit("setData", resp.data);
      commit("setIsLoading", false);
      return resp.data;
    },
    // 登出
    loginOut({ commit }) {
      commit("setData", null);
      commit("setIsLoading", false);
      loginServ.loginOut();
    },
    //
    async whoAmi({ commit }) {
      commit("setIsLoading", true);
      try {
        const resp = await loginServ.whoAmi();
        commit("setData", resp.data);
      } catch {
        commit("setData", null);
      }
      commit("setIsLoading", false);
    },
  },
};
